.subscribe {
    height: auto;
    width: 100%;
    gap: 20px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.subscribe h4 {
    font-style: normal;
    color: #E61A32;
    font-size: 60px;
    margin: auto;
    display: flex;
    font-family: 'Lavishly Yours', cursive;
}

.subscribe h1 {
    font-family: lato;
    font-size: 35px;
    color: gray;
    margin: auto;
}

.subscribeForm {
    padding: 0px;
    padding-left: 10px;
    overflow: hidden;
    width: 600px;
    height: auto;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 50px;
    border: 1px solid lightslategray;
    box-shadow: 5px 0.5em 10px rgba(128, 128, 128, 0.685);
}

.subscribeForm input {
    height: 50px;
    width: 100%;
    font-family: lato;
    font-weight: normal;
    justify-content: center;
    align-items: center;
    margin: auto;
    display: flex;
    border: none;
    outline: none;
    padding: 20px;
}

.subscribeForm input::placeholder {
    font-size: 20px;
    left: -50%;
    /* text-align: center; */
    color: lightgray;
    font-weight: bold;
    margin: auto;
}

.subscribeForm .suscribeButton {
    border-radius: 50%;
    display: flex;
    height: 100%;
    width: auto;
    position: relative;
    align-items: center;
    justify-content: center;
    border: none;
    margin: 10px;
    padding: 10px;
    position: relative;
    background-color: #E61A32;
}

.submitBtnIcon {
    position: relative;
    font-size: 20px;
    border-radius: 50%;
    color: #fff;
    width: 100%;
    height: inherit;
    display: flex;
    margin: auto;
    object-fit: contain;

}