.footer {
    height: 50vh;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
}

.footer .footerImageWrapper {
    position: absolute;
    height: auto;
    width: 100%;
    display: flex;
    margin: auto;
    justify-content: center;
    align-items: center;
    bottom: 0px;
    z-index: -999;
}

.footer .footerImageWrapper img {
    width: 100%;
    height: auto;
    object-fit: contain;
    margin: auto;
    display: flex;
}

.footerLinkWrapper {
    height: 100%;
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    margin: auto;
    justify-content: space-evenly;
    align-items: center;
    /* -webkit-backdrop-filter: blur(5px); */
}